import axios from 'axios'

const url = "https://forrent.herokuapp.com/"

export const getAllProducts = (ext)=>{
  return axios.get(url+ext)
  .then(res=>{
    return res.data
  })
  .catch(e=>{
    return e
  })
}

export const getProductInfo = (ext)=>{
  return axios.get(url+ext)
  .then(res=>{
    return res.data
  })
  .catch(e=>{
    return e
  })
}