import React from 'react'
import {Card,CardContent,CardActionArea,CardMedia,Avatar,Grid} from '@material-ui/core'

const AllProductsDisplay = ({products,showProducts})=>{
  return(
    <div>
      <div>
      <h2 style={{textAlign:'center'}}>Todos los productos disponibles</h2>
      </div>
      <div className='container'>
        <Grid container justify="center" spacing={Number(16)}>
        {products.map((e,i)=>
          <Grid key={i} item> 
          <Card style={{width:'200px',marginLeft:'3%', marginTop:'5%'}}>
          <CardActionArea onClick={()=>showProducts(e)}>
            <CardMedia
              component="img"
              alt="Contemplative Reptile"
              height="180"
              image={e.photoURL}
              title="Contemplative Reptile"
            />
            <CardContent>
              <Avatar aria-label="Recipe">
                R
              </Avatar>
              <p>{e.title}</p>
              <p>${e.price} por día</p>          
            </CardContent>
          </CardActionArea>
        </Card>
        </Grid>
        )}
        </Grid>
      </div>
    </div>
  )
}

export default AllProductsDisplay